#strength {
  color: #2a28a9;
}
.strengthcontainer {
  height: 20vh;
  display: grid;
  place-items: center;
  font-size: 55px;
  font-family: "Open Sans", sans-serif;
  scroll-snap-align: start;
}
.strengthcardcontainer {
  height: 60vh;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  align-items: center;
  justify-items: center;
  position: relative;
  /* overflow: hidden; */
}
#topcarddiv {
  justify-self: center;
}
.bottomcardcontainer {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}
.strengthcardcontainer::before {
  transform: translateX(89%);
  content: "";
  position: absolute;
  right: 50%;
  top: -136px;
  width: 510px;
  height: 551px;
  background-image: url("./FlyingDoodle.png");
  background-repeat: no-repeat;
  background-size: 601px;
  z-index: -2;
}
.strengthextraspace {
  height: 20vh;
}
@media only screen and (max-width: 750px) {
  .strengthcardcontainer::before {
    display: none;
  }
}
@media only screen and (max-width: 910px) {
  .strengthcardcontainer::before {
    transform: translateX(69%);
  }
}
