#solutioncolor {
  color: #2a28a9;
}

.solutioncontainer {
  scroll-snap-align: start;
  height: 20vh;
  display: grid;
  place-items: center;
  font-weight: 500;
  font-size: 55px;
  line-height: 70px;
  font-family: "Open Sans", sans-serif;
}
.cardcontainer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1em;
  grid-column: 2;
}
.maintaingridflow {
  display: grid;
  grid-template-columns: 5% 1fr 5%;
  height: 60vh;
}
.solutionbuttoncontainer {
  display: grid;
  grid-template-columns: 5% 1fr 5%;
  height: 20vh;
}
.solutionbuttoncontainer > button {
  grid-column: 2;
  width: 100%;
  background: #3b38d0;
  box-shadow: -3px -3px 5px rgba(0, 0, 0, 0.25),
    inset 0px -6px 7px rgba(142, 141, 227, 0.76),
    inset 0px 7px 6px rgba(45, 42, 181, 0.68);
  border-radius: 8px;
  border: none;
  padding: 14px 0;
  color: white;
  font-weight: 500;
  font-size: 20px;
  line-height: 25px;
  align-self: flex-start;
}
@media only screen and (max-width: 690px) {
  .cardcontainer {
    grid-template-columns: 1fr;
  }
  .solutionbuttoncontainer > button {
    align-self: center;
  }
}
