.dynacard {
  width: 100%;
  height: 150px;
  background: #ffffff;
  box-shadow: 3px 5px 25px 2px rgba(42, 40, 169, 0.24);
  border-radius: 7px;
  display: grid;
  grid-template-columns: 1fr 3fr;
  padding: 30px 20px;
  font-family: "Open Sans", sans-serif;
  position: relative;
  min-width: 300px;
  overflow: hidden;
}

.dynacardleft {
  display: flex;
  justify-content: center;
  align-items: center;
}
.dynacardleft img {
  width: 60px;
  max-width: 60px;
}
.dynacardright {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.dcardtitle {
  font-size: 26px;
  color: #2a28a9;
}
.dcardcontent {
  font-size: 14px;
  word-wrap: break-word;
  width: 220px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media only screen and (max-width: 950px) {
  .dcardcontent {
    width: 170px;
  }
}
@media only screen and (min-width: 950px) {
  .dcardcontent {
    width: 300px;
  }
}
@media only screen and (max-width: 700px) {
  .dcardcontent {
    width: 120px;
  }
  .dcardtitle {
    font-size: 20px;
  }
}
@media only screen and (max-width: 796px) {
  .dcardtitle {
    font-size: 23px;
  }
}
@media only screen and (max-width: 690px) {
  .dynacard {
    height: 100px;
  }
}
.dynacard:hover .slider {
  width: 30%;
  right: 0;
  transition: 0.6s all ease;
}

.slider {
  position: absolute;
  right: -220px;
  top: 0;
  width: 30%;
  height: 100%;
  background: #3a3890;
  box-shadow: -30px -1px 18px 1px rgba(0, 0, 0, 0.2);
  border-radius: 0px 7px 7px 0px;
  transition: 0.6s all ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
}
.slider > div:nth-child(1) {
  width: 50px;
  height: 50px;
  background-color: #fff;
  box-shadow: 4px 4px 4px rgba(59, 56, 208, 0.25),
    inset -2px -4px 3px rgba(0, 0, 0, 0.13);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.slider > div:nth-child(1) img {
  transform: rotateY(180deg);
}
