.navbar {
  display: grid;
  grid-template-columns: 5% 1fr 3fr 5%;
  height: 10vh;
  scroll-snap-align: start;
}
#logo {
  grid-column: 2;
  align-self: center;
  font-weight: bold;
  font-size: 24px;
  line-height: 31px;
  color: #3b38d0;
}
.navlinks {
  justify-self: end;
  align-self: center;
}
.navlinks a {
  text-decoration: none;
  color: #3b38d0;
  margin: 0 1.15rem;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
}
.navlinks a:active {
  color: #3b38d0;
}
.CTA {
  background: #3b38d0;
  box-shadow: inset 0px -5px 4px rgba(100, 98, 214, 0.91),
    inset 0px 4px 4px -1px rgba(42, 40, 169, 0.83);
  border-radius: 9px;
  color: white !important;
  padding: 7px 17px;
}

@media only screen and (max-width: 950px) {
  .navlinks {
    display: none;
  }
}
