#excel {
  color: #2a28a9;
}
.excelheader {
  height: 20vh;
  display: grid;
  place-items: center;
  font-weight: 500;
  font-size: 55px;
  line-height: 70px;
  font-family: "Open Sans", sans-serif;
  scroll-snap-align: start;
}
.successstories {
  height: 40vh;
  display: grid;
  grid-template-columns: 5% 1fr 5%;
  width: 100%;
}
.successcontent {
  grid-column: 2;
  background: #3b38d0;
  color: white;
  box-shadow: -3px -3px 5px rgba(0, 0, 0, 0.25),
    inset 0px -6px 7px rgba(142, 141, 227, 0.76),
    inset 0px 7px 6px rgba(45, 42, 181, 0.68);
  border-radius: 8px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.successcontent > div:nth-child(3) > img {
  width: 300px;
  object-fit: cover;
}
.successcontent > div:nth-child(3) {
  display: flex;
  justify-content: center;
}
.successcontent > div:nth-child(1) {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.successheading {
  font-size: 200px;
  margin-top: -90px;
}

.successcontent > div:nth-child(1) p {
  margin-top: -35px;
  font-size: 20px;
}
.successcontent > div:nth-child(2) {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.successmiddlecolumn {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.successmiddlecolumn button {
  border: none;
  background: #ffffff;
  box-shadow: inset 4px 4px 6px rgba(0, 0, 0, 0.25);
  border-radius: 7px;
  width: 100%;
  height: 60px;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 38px;
  text-transform: capitalize;
  margin-top: 20px;
  color: #2a28a9;
}
.successmiddlecolumn h3 {
  font-weight: bold;
  font-size: 33px;
  line-height: 42px;
  text-transform: capitalize;
}
.successmiddlecolumn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 80%;
  height: 8px;
  background-color: white;
  margin-top: -10px;
  border-radius: 5px;
}
.successcards {
  width: 100%;
  height: 40vh;
  display: grid;
  grid-template-columns: 5% 1fr 5%;
}
.successcardlayout {
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-column: 2;
}
