.successcardholder {
  width: 23%;
  height: 60%;
  background: #faf6f3;
  box-shadow: 3px 5px 25px 2px rgba(42, 40, 169, 0.2);
  border-radius: 7px;
  padding: 1em;
  overflow: hidden;
  font-family: "Open Sans", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.scardtitle {
  font-size: 18px;
  margin-bottom: 4px;
}
.scardsubtitle {
  font-size: 18px;
  margin-bottom: 8px;
}
.scardcontent {
  font-size: 14px;
}
