.strengthcard {
  width: 330px;
  height: 110px;
  background: #3b38d0;
  box-shadow: -3px -3px 5px rgba(0, 0, 0, 0.25),
    inset 0px -6px 7px rgba(142, 141, 227, 0.76),
    inset 0px 7px 6px rgba(45, 42, 181, 0.68);
  border-radius: 8px;
  color: white;
  display: grid;
  font-size: 40px;
  grid-template-columns: 1fr 3fr;
}
.strengthleft {
  justify-self: center;
  align-self: center;
}
.strengthleft > img {
  padding-left: 26px;
}
.strengthright {
  justify-self: center;
  align-self: center;
  padding-right: 10px;
}
.strengthtitle {
  text-align: center;
  text-transform: capitalize;
  font-weight: bold;
  margin-top: -10px;
}
.strengthcontent {
  font-size: 18px;
  text-transform: capitalize;
  line-height: 23px;
  font-weight: bold;
}
