.productheader {
  height: 10vh;
  display: grid;
  place-items: center;
  font-weight: 500;
  font-size: 55px;
  line-height: 70px;
  font-family: "Open Sans", sans-serif;
  color: #000000;
  scroll-snap-align: start;
}
#productscolor {
  color: #2a28a9;
}
.productgrid {
  position: relative;
  display: grid;
  grid-template-columns: 5% repeat(3, 1fr) 5%;
  height: 70vh;
  overflow: hidden;
}
.productgrid > div:nth-child(1) {
  grid-column: 2;
}
.gridrow {
  align-self: center;
  justify-self: center;
}
.card {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
}
#seeproductbutton {
  background: #3b38d0;
  box-shadow: -3px -3px 5px rgba(0, 0, 0, 0.25),
    inset 0px -6px 7px rgba(142, 141, 227, 0.76),
    inset 0px 7px 6px rgba(45, 42, 181, 0.68);
  border-radius: 8px;
  color: white !important;
  padding: 13px 25px;
  border: none;
  outline: none;
  width: 100%;
  /* align-self: flex-end; */
  /* justify-self: start; */
}
.middlecolumn {
  display: flex;
  flex-direction: column;
  /* width: 80%; */
  justify-content: space-between;
  align-items: center;
}
.productgrid::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  /* top: 50%; */
  /* transform: translate(-25%, -25%); */
  width: 461px;
  height: 100%;
  background-image: url("./Man.png");
  background-repeat: no-repeat;
  background-size: 386px;
  object-fit: contain;
  margin-top: 132px;
  margin-left: 25px;
  z-index: -1;
}
.doodlecontainer {
  height: 20vh;
  display: flex;
  position: relative;
}
.leftdoodle {
  position: absolute;
  top: -65px;
  left: 0;
  margin-left: 100px;
  width: 180px;
  object-fit: contain;
  z-index: -1;
}
.rightdoodle {
  position: absolute;
  right: 10px;
  top: 0;
  width: 300px;
  margin-top: -210px;
  object-fit: contain;
  z-index: -1;
}
.card > h3 {
  font-size: 26px;
  line-height: 33px;
  text-align: center;
  color: #000000;
}
.card p {
  font-size: 14px;
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: normal;
  line-height: 150%;
  text-align: center;
}
