.lowestcontainer {
  scroll-snap-align: start;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.upper {
  height: 60vh;
  display: grid;
  grid-template-columns: 5% 1fr 5%;
}
.upper > div:nth-child(1) {
  grid-column: 2;
  background: #3b38d0;
  box-shadow: -3px -3px 5px rgba(0, 0, 0, 0.25),
    inset 0px -6px 7px rgba(142, 141, 227, 0.76),
    inset 0px 7px 6px rgba(45, 42, 181, 0.68);
  border-radius: 8px;
  height: 80%;
  align-self: center;
  display: flex;
  justify-content: center;
}
.infocontainer {
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-content: center;
}
.infocontainer h3 {
  color: white;
  font-weight: bold;
  font-size: 37px;
  line-height: 47px;
  text-align: center;
}
.infocontainer input[type="text"] {
  height: 50px;
  font-size: 20px;
  padding-left: 20px;
  outline: none;
  font-weight: 600;
  color: #2d2d2d;
  font-family: "Open Sans", sans-serif;
  position: relative;
  width: 100%;
  border-radius: 10px;
  border: none;
}

#email {
  color: white;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  font-family: "Open Sans", sans-serif;
  margin-bottom: 10px;
}
.emailcontainer {
  height: 80px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
.subscribe {
  width: 200px;
  height: 40px;
  background: #ffffff;
  box-shadow: 3px 4px 7px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  border: none;
  outline: none;
  color: #3b38d0;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  margin: 0 auto;
}
.lower {
  height: 40vh;
  background-color: #3b38d0;
  display: grid;
  grid-template-columns: 5% repeat(4, 1fr) 5%;
  align-items: center;
  justify-items: center;
  color: white;
}
.lower div:nth-child(1) {
  grid-column: 2;
}
.lowercards {
  height: 100%;
  width: 100%;
}
.lowercardholder {
  width: 80%;
  height: 80%;
}
.lowercards h3 {
  padding: 15px 0;
}
.lowercards p {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  padding-bottom: 8px;
  color: #d8d8d8;
}
.info {
  display: grid;
  grid-template-columns: 20% 1fr;
  margin-bottom: 10px;
}
