.hero {
  display: grid;
  grid-template-columns: 5% 1fr 1fr 5%;
  height: 70vh;
  /* max-height: 70vh; */
  /* overflow: hidden; */
}
.left {
  grid-column: 2;
}
.left a {
  text-decoration: none;
}
.left {
  display: grid;
}

#headings {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.buttonCTA {
  background: #3b38d0;
  box-shadow: inset 0px -6px 7px rgba(142, 141, 227, 0.76),
    inset 0px 7px 6px rgba(45, 42, 181, 0.68);
  border-radius: 8px;
  color: white !important;
  padding: 13px 25px;
  border: none;
  outline: none;
  align-self: flex-end;
  justify-self: start;
}
.left h3 {
  font-size: 55px;
}
#idandsec {
  color: #3b38d0;
}
#tableboy {
  width: 100%;
  height: auto;
  max-width: 522px;
  object-fit: cover;
}
.footercontainer {
  height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 950px) {
  .left h3 {
    font-size: 36px;
  }
  .buttonCTA {
    align-self: flex-start;
  }
}
@media only screen and (max-width: 700px) {
  .right {
    display: none;
  }
  .hero {
    grid-template-columns: 5% 1fr 5%;
  }
  .left {
    justify-items: center;
  }
  .buttonCTA {
    justify-self: center;
    width: 60%;
  }
}
