#featurecolor {
  color: #2a28a9;
}
.snapscroll {
  scroll-snap-align: start;
}
.workcontainer {
  height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
  /* font-weight: 500; */
  font-size: 55px;
  line-height: 70px;
  font-family: "Open Sans", sans-serif;
}
.scrollableworkcontainer {
  display: grid;
  grid-template-columns: 5% 10% 1fr 10% 5%;
  height: 50vh;
}
.workimgcontainer {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1em;
  align-items: center;
  justify-items: center;
}
.workimgcontainer img {
  width: 130px;
  object-fit: contain;
}
.arrow {
  width: 50px;
  height: 50px;
  background: #bcbcec;
  box-shadow: 4px 4px 4px rgba(59, 56, 208, 0.25),
    inset -2px -4px 3px rgba(0, 0, 0, 0.13);
  transform: matrix(-1, 0, 0, 1, 0, 0);
  border-radius: 50%;
  display: grid;
  place-items: center;
}
.leftarrow {
  grid-column: 2;
  transform: rotateY(180deg);
  justify-self: center;
  align-self: center;
}
.rightarrow {
  grid-column: 4;
  justify-self: center;
  align-self: center;
}
.extraspace {
  height: 30vh;
}
